.input-group {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.input-container {
  flex: 1; 
  margin-right: 20px; 
}

.input-container:last-child {
  margin-left: 20px; 
}

.input-text {
  width: 100%; 
  flex: 1; 
  font-family: 'Courier New', Courier, monospace;
  font-size: large;
  border: 1px solid #ddd;
  border-radius: 4px;
  padding: 10px;
  background-color: #f7f4f4;
}

label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}


textarea {
  width: 100%; 
  max-height: 300px; 
  min-height: 300px; 
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 16px;
  resize: vertical;
  overflow-y: auto; 
  transition: border 1s;
}

textarea:focus {
  border-color: #5a9;
  outline: none;
}

.button-group {
  display:flexbox;
  justify-content: space-between;
  margin-top: 20px;
}

button {
  padding: 10px 20px;
  width: 200px;
  margin: 20px;
  font-size: 20px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
}

.compare-button {
  background-color: #31618d; 
  color: white;
}

.compare-button:hover {
  background-color: #0d3f81; 
}

.convert-button {
  background-color: #31618d; 
  color: white;
}

.convert-button:hover {
  background-color: #0d3f81; 
  color: white;
}

.clear-button {
  background-color: #31618d; 
  color: white;
}

.clear-button:hover {
  background-color: #f07d7b; 
}

.output-container {
  flex: 1; 
  margin-right: 10px; 
  margin-left: 10px; 
  background-color: #f7f4f4;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.output-text {

  flex: 1; 
  font-family: 'Courier New', Courier, monospace;
  font-size: large;
  border: 1px solid #ddd;
  border-radius: 4px;
  border-color: rgb(160, 158, 158);
  padding: 10px;
  background-color: #f7f4f4;
  margin-right: 20px; 
  margin-left: 20px; 
}

.output-textarea {
  width: 95%; 
  max-height: 300px; 
  min-height: 300px; 
  padding: 12px;
  margin: 10px 0;
  border: 1px solid #ddd;
  border-radius: 4px;
  border-color: rgb(160, 158, 158);
  font-size: 16px;
  resize: vertical;
  overflow-y: auto; 
  transition: border 1s;
}

.download-button{
  margin: 0px;
  font-size: 15px;
  margin-left: 10px;
  margin-right: 20px;
  border: 1px solid #838181;
  border-radius: 4px;
  cursor: pointer;
  background-color: #c9c6c6;
  font-family: 'Courier New', Courier, monospace;
  color: black;
  transition: background-color 0.3s;
  justify-content: space-between;
  margin-top: 0px;
}
.download-button:hover {
  background-color: #e9e0e0; 
}

.base-button-group{
  width: 100%;
  margin: 0px;
  font-size: 5px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  display:flex;
  justify-content: space-between;
  margin-top: 0px;
}

.base-button-subgroup{
  width: 100%;
  margin: 0px;
  font-size: 7px;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  transition: background-color 0.3s;
  justify-content: space-between;
  margin-top: 0px;
}

.base-button{
  margin: 0px;
  font-size: 15px;
  margin-left: 5px;
  margin-right: 10px;
  margin-bottom: 10px;
  border: 1px solid #a5a5a5;
  border-radius: 4px;
  cursor: pointer;
  background-color: #f7f4f4;
  font-family: 'Courier New', Courier, monospace;
  color: black;
  transition: background-color 0.3s;
  justify-content: space-between;
  margin-top: 0px;
}
.base-button:hover {
  background-color: #e9e0e0; 
}